import React, { Component } from "react";
import Typical from "react-typical";
import Switch from "react-switch";
import GreetingLottie from "../components/DisplayLottie";

class Header extends Component {
  titles = [];

  constructor() {
    super();
    this.state = { checked: false };
    this.onThemeSwitchChange = this.onThemeSwitchChange.bind(this);
  }

  onThemeSwitchChange(checked) {
    this.setState({ checked });
    this.setTheme();
  }

  setTheme() {
    var dataThemeAttribute = "data-theme";
    var body = document.body;
    var newTheme =
      body.getAttribute(dataThemeAttribute) === "dark" ? "light" : "dark";
    body.setAttribute(dataThemeAttribute, newTheme);
  }

  render() {
    if (this.props.sharedBasicInfo) {
      this.titles = this.props.sharedBasicInfo.titles.map(x => [x.toUpperCase(), 1500]).flat();
    }
    if (this.props.resumeBasicInfo) {
      var name = this.props.resumeBasicInfo.name;
    }

    const HeaderTitleTypeAnimation = React.memo(() => {
      return <Typical className="title-styles" steps={this.titles} loop={50} />
    }, (props, prevProp) => true);

    return (
      <header>
        <div className="row aligner">
          <div className="col-md-12">
            <div>
              <div className="col-sm-11 col-md-4 mx-auto ">
                <GreetingLottie animationPath="/lottie/coding.json" />
              </div>

              <h1 className="mb-0 typical-text">
                <Typical steps={[name]} wrapper="p" />
              </h1>

              <div className="title-container">
                <HeaderTitleTypeAnimation />
              </div>

              <Switch
                checked={this.state.checked}
                onChange={this.onThemeSwitchChange}
                offColor="#baaa80"
                onColor="#353535"
                className="react-switch mx-auto mb-4"
                width={90}
                height={40}
                uncheckedIcon={
                  <span
                    className="iconify"
                    data-icon="twemoji:new-moon-face"
                    data-inline="false"
                    style={{
                      display: "block",
                      height: "100%",
                      fontSize: 25,
                      textAlign: "end",
                      marginLeft: "20px",
                      color: "#353239",
                    }}
                  ></span>
                }
                checkedIcon={
                  <span
                    className="iconify"
                    data-icon="noto-v1:sun-with-face"
                    data-inline="false"
                    style={{
                      display: "block",
                      height: "100%",
                      fontSize: 25,
                      textAlign: "end",
                      marginLeft: "10px",
                      color: "#353239",
                    }}
                  ></span>
                }
                id="icon-switch"
              />
            </div>
          </div>
        </div>
      </header>
    );
  }
}

export default Header;
